import React, { useEffect, useState } from "react";
import { Card, CardHeader, Table, Row, Col } from "reactstrap";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { useSelector, useDispatch } from "react-redux";

import Rowf from "./row";
import { getKassaData } from "../../../../redux/action/kassa/tableKassaAction";
const KassaTable = () => {
  const user = useSelector((state) => state.userLogin);
  const { kassa } = useSelector((state) => state.kassa);
  const { userInfo } = user;
  const [data, setData] = useState([]);

  const dispatch = useDispatch();
  useEffect(() => {
    const getDataTable = async () => {
      await dispatch(getKassaData());
    };
    getDataTable();
  }, []);

  console.log(kassa);

  return (
    <Row className="mt-5">
      <Col xl="12">
        <Card className="shadow" style={{ height: "100vh", padding: 10 }}>
          <CardHeader className="border-0">
            <Row className="align-items-center">
              <div className="col">
                <h3 className="mb-0"></h3>
              </div>
              <div className="col text-right">
                <ReactHTMLTableToExcel
                  id="test-table-xls-button"
                  className="download-table-xls-button excel"
                  table="table-to-xls"
                  filename="Xarid"
                  sheet="tablexls"
                  buttonText=" "
                />
              </div>
              {/* <DropdownMenu /> */}
            </Row>
          </CardHeader>
          <Table
            id="table-to-xls"
            className="align-items-center table-flush"
            responsive
            // style={{ overflowY: 'scroll' }}
          >
            <thead className="thead-light">
              <tr>
                <th>ID</th>
                <th></th>
                <th></th>
                <th></th>
                <th> </th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th>()</th>
                <th>()</th>
                <th>X/()</th>
                <th>()</th>
                {userInfo?.isAdmin ? <th></th> : ""}
              </tr>
            </thead>
            <tbody>
              {kassa.map((row, index) => (
                <Rowf row={row} key={index} />
              ))}
            </tbody>
          </Table>
        </Card>
      </Col>
    </Row>
  );
};

export default KassaTable;
